@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff") format("woff");
  src: url("../fonts/ptsans.woff2") format("woff2");
  font-display: swap;
  font-size: 14px;
  line-height: 21px;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff") format("woff");
  src: url("../fonts/ptsansbold.woff2") format("woff2");
  font-display: swap;
}
.page__body {
  font-family: "PT Sans", sans-serif;
  margin: 0;
  min-height: 100%;
  background-color: #E7E5E5;
}

.page__container {
  margin: 0 auto;
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  box-shadow: 0 0 15px #00000033;
  max-width: 1200px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header {
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  position: relative;
  max-height: 56px;
}
@media (min-width: 768px) {
  .main-header {
    flex-direction: row;
    height: 56px;
  }
}
@media (min-width: 1200px) {
  .main-header {
    flex-direction: row;
    height: 56px;
    width: 1200px;
  }
}

.main-header__inner {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 56px;
  box-sizing: border-box;
  width: 100%;
}

.main-header__logo {
  position: absolute;
  z-index: 3;
}

.main-nav {
  width: inherit;
  z-index: 5;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    flex-direction: row;
    width: inherit;
    z-index: 1;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    display: flex;
    flex-direction: row;
  }
}

.main-nav__list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  padding: 0;
  width: 100%;
  margin: 0;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

.main-nav__toggle {
  display: block;
  position: absolute;
  right: 20px;
  top: 17px;
  width: 23px;
  height: 22px;
  z-index: 2;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 5px;
  right: 20px;
  width: 23px;
  height: 4px;
  background-color: #81B3D2;
  box-shadow: 0 8px 0 0 #81B3D2, 0 16px 0 0 #81B3D2;
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: fade(#ffffff, 30%);
  box-shadow: 0 8px 0 0 fade(#ffffff, 30%), 0 16px 0 0 fade(#ffffff, 30%);
}

.main-nav--closed .main-nav__list {
  display: none;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__list {
    display: flex;
  }
}

.main-nav--opened .main-nav__toggle {
  top: 0;
  left: auto;
  right: 0;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  right: 20px;
  width: 36px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

