.social__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.social__item {
  width: 48px;
  height: 48px;
  background-color: #81B3D2;

  &:hover {
    background-color: #669EC0;
  }

  &:active {
    background-color: #5496BD;
  }
}

.social__logo:hover {
  fill: #tomato;
}

.social__logo:active {
  fill: #tomato;
}